// initilise variables
const choices = ['rock', 'paper', 'scissors'];
let userScore = 0;
let computerScore = 0;

// randomly select computerr choice from array
function getcomputerSelection() {
  return choices[Math.floor(Math.random() * choices.length)];
}

function playRound(computerSelection, playerSelection) {
  // messages to be returned to user at the end of the round
  const winMsg = `You win the round! ${playerSelection} beats ${computerSelection}`;
  const loseMsg = `You lose the round! ${computerSelection} beats ${playerSelection}`;
  const drawMsg = `Tie round. You both chose ${playerSelection}`;

  if (computerSelection === 'rock' && playerSelection === 'paper') {
    userScore += 1;
    return winMsg;
  }
  if (computerSelection === 'rock' && playerSelection === 'scissors') {
    computerScore += 1;
    return loseMsg;
  }
  if (computerSelection === 'paper' && playerSelection === 'scissors') {
    userScore += 1;
    return winMsg;
  }
  if (computerSelection === 'paper' && playerSelection === 'rock') {
    computerScore += 1;
    return loseMsg;
  }
  if (computerSelection === 'scissors' && playerSelection === 'rock') {
    userScore += 1;
    return winMsg;
  }
  if (computerSelection === 'scissors' && playerSelection === 'paper') {
    computerScore += 1;
    return loseMsg;
  }

  return drawMsg;
}

function game() {
  // set both player scores to 0
  userScore = 0;
  computerScore = 0;

  // add event listeners to choice buttons
  const UserChoiceButtons = document.querySelectorAll('.user-selection-btn');

  // play round msg container
  const msgRoundContainer = document.querySelector('.msg-round-container');
  const msgGameContainer = document.querySelector('.msg-game-container');
  const scoreContainer = document.querySelector('.score-container');

  function endGame() {
    if (userScore > computerScore) {
      msgRoundContainer.textContent = `Congratulations! You won the game ${userScore} rounds to ${computerScore}`;
    }
    msgGameContainer.textContent = `You lost the game ${computerScore} rounds to ${userScore}`;
  }

  function checkScore() {
    if (userScore === 5 || computerScore === 5) {
      endGame();
    }
  }

  function userPlay(userChoice) {
    if (userScore < 5 && computerScore < 5) {
      const computerSelection = getcomputerSelection(); // assign computer choice to variable

      msgRoundContainer.textContent = playRound(computerSelection, userChoice);

      scoreContainer.textContent = `${userScore}:${computerScore}`;

      checkScore();
    }

    // display running game score
  }

  UserChoiceButtons.forEach((btn) => {
    btn.addEventListener('click', function () {
      userPlay(this.value);
    });
  });
}
game();

// display results
